export const getAllGoods = (goods) => {
    return {
        type: 'GET_ALL_GOODS',
        payload: goods
    }
}

export const error = () => {
    return {
        type: 'ERROR'
    }
}

export const addCartGoods = (newArr) => {
    return {
        type: 'ADD_CART_GOODS',
        payload: newArr
    }
}