import {Container, Row, Col} from 'react-bootstrap';
import {useSelector, useDispatch} from 'react-redux';
import {addCartGoods} from '../../actions/action';

import '../style/close.scss';
import './cartItem.scss';

import close from '../images/close.svg';

const CartItem = ({item}) => {

    const cart = useSelector(state => state.cart);
    const dispatch = useDispatch();

    const {id, name, img, quantity, summa} = item;

    let newArr = [...cart];

    const delQuantity = (id) => {
        newArr.map((item, i) => {
            if(item.id == id) {
                if(item.quantity > 1) {
                    let elem = item;
                    if(id == 3) {
                        if(item.quantity % 3) {
                            newArr.splice(i, 1, {...elem, quantity: elem.quantity - 1, summa: elem.summa - elem.cost})
                        } else {
                            newArr.splice(i, 1, {...elem, quantity: elem.quantity - 1, summa: elem.summa - (elem.cost / 2)})
                        }
                    } else {
                        newArr.splice(i, 1, {...elem, quantity: elem.quantity - 1, summa: elem.summa - elem.cost})
                    }
                } else {
                    return newArr.splice(i, 1);
                }
            }
        })
    }

    const addQuantity = (id) => {
        newArr.map((item, i) => {
            if(item.id == id) {
                const elem = item;
                if(id == 3) {
                    if((item.quantity + 1) % 3) {
                        newArr.splice(i, 1, {...elem, quantity: elem.quantity + 1, summa: elem.summa + elem.cost})
                    } else {
                        newArr.splice(i, 1, {...elem, quantity: elem.quantity + 1, summa: elem.summa + (elem.cost / 2)})
                    }
                } else {
                    newArr.splice(i, 1, {...elem, quantity: elem.quantity + 1, summa: elem.summa + elem.cost})
                }
            }
        })
    }

    const deleteItem = (id) => {
        newArr.map((item, i) => {
            if(item.id == id) {
                newArr.splice(i, 1)
            }
        })
    }


    return (
        <div className="cart-item">
            <Container>
                <Row>
                    <Col className="col-12 col-md-6 m-auto">
                        <div className="cart-item__block position-relative">
                            <div className="d-flex justify-content-between align-items-center">
                                <img className="cart-item__image" src={img} alt={name}/>
                                <span>{name}</span>
                                <span>К оплата: {summa}$</span>
                                <button className="btn btn-primary" onClick={() => {
                                    delQuantity(id);
                                    dispatch(addCartGoods(newArr));
                                }}>-</button>
                                <span>Количество: {quantity}кг</span>
                                <button className="btn btn-primary" onClick={() => {
                                    addQuantity(id);
                                    dispatch(addCartGoods(newArr));
                                }}>+</button>
                            </div>
                            <button className='close' onClick={() => {
                                deleteItem(id);
                                dispatch(addCartGoods(newArr));
                            }}>
                                <img src="https://www.svgrepo.com/show/349933/x.svg" alt="close" />
                            </button>
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default CartItem;