import {useSelector} from 'react-redux';
import { v4 as uuidv4 } from 'uuid';
import CartItem from '../cartItem/CartItem';
import CartSumma from '../cartSumma/CartSumma';

import './cart.scss';

const Cart = () => {

    const cart = useSelector(state => state.cart);

    const content = cart.length ? cart.map(item => {return <CartItem item={item} key={uuidv4()}/>}) : <h2>Нужно выбрать товар</h2>
    const summa = cart.length ? <CartSumma/> : null;

    return (
        <section className='cart'>
            {content}
            {summa}
        </section>
    )
}

export default Cart;