import {Col} from 'react-bootstrap';
import {useSelector, useDispatch} from 'react-redux';
import {addCartGoods} from '../../actions/action';

import './goodsItem.scss';

const GoodsItem = ({goodsItem}) => {
    const {name, cost, img} = goodsItem;

    const cart = useSelector(state => state.cart);
    const dispatch = useDispatch();

    let newArr = [...cart];

    const filterGoodsItem = (goodsItem) => {
        const {id} = goodsItem;
        if(newArr.length) {
            let isProductExist = false;
            // newArr.map(item => {
            //     if(item.id == id) {
            //         isProductExist = true;
            //         let res = item.summa + item.cost;
            //         return {...item, quantity: item.quantity++, summa: res}
            //     }
            // })
            newArr.map((item, i) => {
                if(item.id == id) {
                    isProductExist = true;
                    const elem = item;
                    if(id == 3) {
                        if((item.quantity + 1) % 3) {
                            newArr.splice(i, 1, {...elem, quantity: elem.quantity + 1, summa: elem.summa + elem.cost})
                        } else {
                            newArr.splice(i, 1, {...elem, quantity: elem.quantity + 1, summa: elem.summa + (elem.cost / 2)})
                        }
                    } else {
                        newArr.splice(i, 1, {...elem, quantity: elem.quantity + 1, summa: elem.summa + elem.cost})
                    }
                }
            })
            if(!isProductExist) {
                newArr.push({...goodsItem, quantity: 1, summa: goodsItem.cost})
            }

        } else {
            newArr.push({...goodsItem, quantity: 1, summa: goodsItem.cost})
        }
    }

    return (
        <Col>
            <div className='goods-item'>
                <img src={img} alt={name}/>
                <h3>{name}</h3>
                <div>Цена: {cost}$</div>
                <button className='btn btn-primary' onClick={() => {
                    filterGoodsItem(goodsItem);
                    dispatch(addCartGoods(newArr));
                }}>Купить</button>
            </div>
        </Col>
    )
}

export default GoodsItem;