const initialState = {
    goods: [],
    loading: true,
    cart: []
}

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case "GET_ALL_GOODS":
            return {
                ...state,
                goods: action.payload,
                loading: false
            }
        case "ERROR":
            return {
                ...state,
                loading: 'error',
            }
        case "ADD_CART_GOODS":
            return {
                ...state,
                cart: action.payload
            }
        default:
            return state;
    }
}

export default reducer;