import { Container, Row, Col } from 'react-bootstrap';
import {useSelector} from 'react-redux';

import './cartSumma.scss'

const CartSumma = () => {

    const cart = useSelector(state => state.cart);

    const summa = (cart) => {
        let s = 0;
        cart.forEach(item => {

            s += item.summa
        });
        return s
    }

    return (
        <Container>
            <Row className='d-flex  justify-content-center'>
                <Col className='col-12 col-md-6'>
                    <div className='d-flex align-items-center justify-content-end cart__summa'>
                        <h4>Всего к оплате:</h4>
                        <h4 className='text-danger ms-3'>{summa(cart)}$</h4>
                    </div>
                </Col>
            </Row>
        </Container>

    )
}

export default CartSumma;