import {Container, Row, Col} from 'react-bootstrap';
import {useSelector} from 'react-redux';
import { Link } from "react-router-dom";

import './header.scss';
import '../style/icon.scss';
import '../style/logo.scss';

import cartImg from '../images/cart.svg';
import logo from '../images/logo.png';

const Header = () => {

    const cart = useSelector(state => state.cart);
    return (
        <header>
            <Container>
                <Row>
                    <Col className='d-flex ms-auto justify-content-center position-relative'>
                        <Link to="/">
                            <img className='logo' src={logo} alt=""/>
                        </Link>

                        <div className="header-btn_wrapper">
                            <Link className='header__btn' to='/cart'>
                                <img className='icon__cart' src={cartImg} alt="cart"/>
                                {
                                    cart.length == 0 ? null : <p className='header__num'>{cart.length}</p>
                                }
                            </Link>
                        </div>
                    </Col>
                </Row>
            </Container>
        </header>
    )
}

export default Header;