import {useEffect} from 'react';
import {Container, Row} from 'react-bootstrap';
import GoodsItem from '../goodsItem/GoodsItem';
import useHttp from '../../http/http';
import Spinner from '../spinner/Spinner';
import {useSelector, useDispatch} from 'react-redux';
import {getAllGoods, error} from '../../actions/action';
import { v4 as uuidv4 } from 'uuid';

import './goods.scss';

const Goods = () => {

    const request = useHttp();

    const {goods, loading} = useSelector(state => state);
    const dispatch = useDispatch();

    const allGoods = [
        {
            "id": 1,
            "name": "Банан",
            "cost": 10,
            "img": "https://www.svgrepo.com/show/227289/banana.svg"
        },
        {
            "id": 2,
            "name": "Яблоко",
            "cost": 8,
            "img": "https://www.svgrepo.com/show/41925/apple.svg"
        },
        {
            "id": 3,
            "name": "Папайя",
            "cost": 10,
            "img": "https://www.svgrepo.com/show/286047/papaya.svg"
        }
    ]

    useEffect(() => {
        dispatch(getAllGoods(allGoods))
        // request('http://localhost:3001/goods')
        //     .then(allGoods => dispatch(getAllGoods(allGoods)))
        //     .catch(() => dispatch(error()))
    }, [])

    const content = () => {
        if(loading == true) {
            return <Spinner/>
        } if (loading == 'error') {
            return <h2>Error</h2>
        } else {
            return goods.map(item => {
                    return <GoodsItem goodsItem={item} key={uuidv4()}/>
                })
        }
    }

    return (
        <section className='goods__wrapper'>
            <Container>
                <Row className="d-flex justify-content-between">
                    {content()}
                </Row>
            </Container>
        </section>
    )
}

export default Goods;